
const cartSlider = document.querySelector('.cart-slider .swiper-wrapper');

let cartQuantity = 18;
let dataLength = null;

const randomId = () => {
  return Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
};

const normalPrice = (str) => {
  return String(str).replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ');
};

const priceWithoutSpaces = (str) => {
  return str.replace(/\s/g, '');
};

if (cartSlider) {
  const cartProducts = (quantity = 6) => {
    fetch('../data/data.json')
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        dataLength = data.length;

        cartSlider.innerHTML = '';

        for (let i = 0; i < dataLength; i++) {
          if (i < quantity) {
            let item = data[i];
            let id = randomId();
            cartSlider.innerHTML += `

                            <div class="swiper-slide swiper-slide-cart">
                                <article class="product product--rating product--cart" data-id="${id}">
                                    <div class="product__rating">
                                        <svg width="16" height="15" viewBox="0 0 16 15"  xmlns="http://www.w3.org/2000/svg">
                                            <path d="M15.2764 5.28193L10.5439 4.59758L8.42832 0.330127C8.37054 0.213287 8.27548 0.118702 8.15805 0.0612089C7.86355 -0.0834504 7.50567 0.037099 7.35842 0.330127L5.24285 4.59758L0.510317 5.28193C0.379841 5.30047 0.260549 5.36167 0.169216 5.45441C0.0587999 5.56733 -0.00204457 5.71924 5.24622e-05 5.87678C0.00214949 6.03431 0.0670165 6.18457 0.1804 6.29454L3.60445 9.61614L2.7955 14.3064C2.77653 14.4156 2.78867 14.5278 2.83053 14.6304C2.87239 14.733 2.94231 14.8218 3.03235 14.8869C3.12239 14.952 3.22896 14.9906 3.33996 14.9985C3.45096 15.0064 3.56196 14.9831 3.66037 14.9314L7.89337 12.717L12.1264 14.9314C12.2419 14.9926 12.3761 15.013 12.5048 14.9908C12.8291 14.9352 13.0472 14.6291 12.9912 14.3064L12.1823 9.61614L15.6063 6.29454C15.6995 6.20367 15.7611 6.08497 15.7797 5.95515C15.83 5.63059 15.6026 5.33015 15.2764 5.28193Z"/>
                                        </svg>
                                        ${item.rating}
                                    </div>
                                    <div class="product__image-cart">
                                        <img  src="${item.mainImage}" alt="${item.title}">
                                    </div>
                                    <h3 class="mp-reset product__title  product__title--cart">${item.title} <br>"${item.model}"</h3>
                                    <div class="product__price product-price product-price__cart ">
                                        <span class="product-price__current product-price__rating">${normalPrice(item.price)} руб</span>

                                    </div>
                                    <button class="btn btn--stroke btn--stroke-ef btn--rating btn-reset product__btn product__btn--catalog add-to-basket-btn" data-id="${id}">Купить</button>
                                </article>
                            </div>

                            `
          }
        }
        basketCartLogic();
      })

  }

  cartProducts(cartQuantity);
}

const productBtnId = () => {
  const products = document.querySelectorAll('.product');

  products.forEach(el => {
    const idBtn = el.dataset.id;
    const productsBtn = document.querySelector('.product__btn');
    productsBtn.dataset.id = `${idBtn}`;
  })
}

const plusFullPrice = (currentPrice) => {
  return price += currentPrice;
}

const minusFullPrice = (currentPrice) => {
  return price -= currentPrice;
}


const printFullPrice = () => {
  fullPrice.textContent = `${normalPrice(price)} руб`
}

let price = 0;
const fullPrice = document.querySelector('.fullprice');

const basketCartLogic = () => {
  const productsBtn = document.querySelectorAll('.product__btn');
  const basket = document.querySelector('.basket');
  const basketCount = document.querySelector('.basket__count');
  const basketCartProductsList = document.querySelector('.basket-content-cart__list');
  const basketContent = document.querySelector('.basket-content');

  const cartAdd = document.querySelector('.cart-product-add');


  const printCount = () => {
    let length = basketCartProductsList.querySelector('.simplebar-content').children.length;
    basketCount.textContent = length;

    length > 0 ? basketCount.classList.add('basket__count--visible') : basketCount.classList.remove('basket__count--visible')
  }

  const generateBasketProduct = (img, title, price, id) => {
    return `
                <li class="basket-content__item basket-content-cart__item" data-id="${id}">
                    <article class="basket-content-cart__product basket-product basket-product-cart" >
                        <img src="${img}" alt="${title}" class="basket-product-cart__img">
                        <div class="basket-product__text">
                            <h3 class="mp-reset basket-product__title">${title}</h3>
                            <span class="basket-product-cart__price">${normalPrice(price)} руб</span>
                        </div>
                        <button class="basket-product-cart__delete" aria-label="Удалить товар">
                        </button>
                    </article>
                </li>
            `
  }

  productsBtn.forEach(el => {
    el.addEventListener('click', (e) => {
      const self = e.currentTarget;
      let parent = self.closest('.product');
      let id = parent.dataset.id;
      let img = parent.querySelector('.product__image-catalog img').getAttribute('src');
      let title = parent.querySelector('.product__title').textContent;
      let priceNumber = parseInt(priceWithoutSpaces(parent.querySelector('.product-price__current').textContent));

      plusFullPrice(priceNumber);
      printFullPrice();

      document.querySelector('.basket__btn').classList.remove('basket__btn--inactive');

      basketCartProductsList.querySelector('.simplebar-content').insertAdjacentHTML('afterbegin', generateBasketProduct(img, title, priceNumber, id));

      self.classList.add('product__btn--disabled');

      printCount();

    });
  });

  const cart = document.querySelector('.cart-product');
  const cartTitle = document.querySelector('.cart-product__title');
  const cartPrice = document.querySelector('.cart-product__price');

  cartAdd.addEventListener('click', (e) => {
    const self = e.currentTarget;
    let parent = self.closest('.product');
    let id = cart.dataset.id;
    let img = cart.querySelector('.cart-product__bg img').getAttribute('src');
    let title = cart.querySelector('.cart-product__title').textContent;
    let priceNumber = parseInt(priceWithoutSpaces(cart.querySelector('.cart-product__price').textContent));

    plusFullPrice(priceNumber);
    printFullPrice();

    document.querySelector('.basket__btn').classList.remove('basket__btn--inactive');

    basketCartProductsList.querySelector('.simplebar-content').insertAdjacentHTML('afterbegin', generateBasketProduct(img, title, priceNumber, id));

    self.classList.add('product__btn--disabled');

    printCount();

  });

  basketCartProductsList.addEventListener('click', (e) => {

    if (e.target.classList.contains('basket-product-cart__delete')) {
      const self = e.target;
      const parent = self.closest('.basket-content-cart__item');
      const price = parseInt(priceWithoutSpaces(parent.querySelector('.basket-product-cart__price').textContent))
      const id = parent.dataset.id;


      if (document.querySelector(`.add-to-basket-btn[data-id="${id}"]`)) {
        document.querySelector(`.add-to-basket-btn[data-id="${id}"]`).classList.remove('product__btn--disabled');
      }

      if (document.querySelector(`.add-to-basket-btn[data-id="cart"]`)) {
        document.querySelector(`.add-to-basket-btn[data-id="cart"]`).classList.remove('product__btn--disabled');
      }
      parent.remove();

      minusFullPrice(price);
      printFullPrice();

      let num = document.querySelectorAll('.basket-content-cart__item').length

      if (num == 0) {
        basketCount.classList.remove('basket__count--visible');
        basketContent.classList.remove('basket-content--visible');
        document.querySelector('.basket__btn').classList.add('basket__btn--inactive');
      }

      printCount(num);


    }
  });
};

const openOrdermodal = document.querySelector('.basket-content__btn');
const orderCartModalList = document.querySelector('.basket-modal-order__list')
const orderModalQuantity = document.querySelector('.basket-modal-order__qantity span');
const orderModalSumm = document.querySelector('.basket-modal-order__summ span');
const basketCount = document.querySelector('.basket__count');
const basketCartProductsList = document.querySelector('.basket-content-catalog__list');
const basketContent = document.querySelector('.basket-content');

orderCartModalList.addEventListener('click', (e) => {
  const printCount = () => {
    let length = basketCartProductsList.querySelector('.simplebar-content').children.length;
    basketCount.textContent = length;

    length > 0 ? basketCount.classList.add('basket__count--visible') : basketCount.classList.remove('basket__count--visible')
  }

  if (e.target.classList.contains('basket-product-cart__delete')) {
    const self = e.target;
    const parent = self.closest('.basket-content-cart__item');
    const price = parseInt(priceWithoutSpaces(parent.querySelector('.basket-product-cart__price').textContent))
    const id = parent.dataset.id;

    if (document.querySelector(`.add-to-basket-btn[data-id="${id}"]`)) {
      document.querySelector(`.add-to-basket-btn[data-id="${id}"]`).classList.remove('product__btn--disabled');
    }

    if (document.querySelector(`.add-to-basket-btn[data-id="cart"]`)) {
      document.querySelector(`.add-to-basket-btn[data-id="cart"]`).classList.remove('product__btn--disabled');
    }

    parent.remove();
    if (document.querySelector(`.basket-content-cart__item[data-id="${id}"]`)) {
      document.querySelector(`.basket-content-cart__item[data-id="${id}"]`).remove();
    }

    if (document.querySelector(`.basket-content-cart__item[data-id="cart"]`)) {
      document.querySelector(`.basket-content-cart__item[data-id="cart"]`).remove();
    }


    minusFullPrice(price);
    printFullPrice();

    orderModalQuantity.textContent = `${document.querySelectorAll('.basket-modal-order__list .basket-content-cart__item').length} шт`;
    orderModalSumm.textContent = `${fullPrice.textContent}`;

    setTimeout(() => {
      let num = document.querySelectorAll('.basket-content-cart__item').length

      if (num == 0) {
        basketCount.classList.remove('basket__count--visible');
        basketContent.classList.remove('basket-content--visible');
        document.querySelector('.basket__btn').classList.add('basket__btn--inactive');
      }

      printCount(num);
    }, 200)


  }
});


