
const basketBtn = document.querySelector('.basket__btn');
const basketContent = document.querySelector('.basket-content');


basketBtn.addEventListener('click', () => {
  basketContent.classList.toggle('basket-content--visible');
})

document.addEventListener('click', (e) => {
  if (!basketContent.contains(e.target) && basketContent.contains(e.target) && !basketBtn.contains(e.target)) {
    basketContent.classList.remove('basket-content--visible');
  }
})





