const menuBtns = document.querySelectorAll('.btn-catalog');
const drops = document.querySelectorAll('.catalog-list-open');


menuBtns.forEach(el => {
  el.addEventListener('click', (e) => {
    let currentBtn = e.currentTarget;
    let drop = currentBtn.closest('.catalog__list-item').querySelector('.catalog-list-open');
    let arrow = currentBtn.closest('.catalog__list-item').querySelector('.catalog-icon');

    menuBtns.forEach(el => {
      if (el !== currentBtn) {
        el.classList.remove('btn-catalog--active')
      }
    });

    drops.forEach(el => {
      if (el !== drop) {
        el.classList.remove('catalog-list-open--active');
      }
    });




    drop.classList.toggle('catalog-list-open--active');
    currentBtn.classList.toggle('btn-catalog--active');

  })
})

document.addEventListener('click', (e) => {
  if (!e.target.closest('.catalog__list')) {
    menuBtns.forEach(el => {
      el.classList.remove('btn-catalog--active')
    });

    drops.forEach(el => {
      el.classList.remove('catalog-list-open--active');
    });
  }
})
