const productBtnId = () => {
  const products = document.querySelectorAll('.product');

  products.forEach(el => {
    const idBtn = el.dataset.id;
    const productsBtn = document.querySelector('.product__btn');
    productsBtn.dataset.id = `${idBtn}`;
  })
}

const plusFullPrice = (currentPrice) => {
  return price += currentPrice;
}

const minusFullPrice = (currentPrice) => {
  return price -= currentPrice;
}


const printFullPrice = () => {
  fullPrice.textContent = `${normalPrice(price)} руб`
}

let price = 0;
const fullPrice = document.querySelector('.fullprice');


const basketCatalogLogic = () => {
  const productsBtn = document.querySelectorAll('.product__btn');
  const basket = document.querySelector('.basket');
  const basketCount = document.querySelector('.basket__count');
  const basketCatalogProductsList = document.querySelector('.basket-content-catalog__list');
  const basketContent = document.querySelector('.basket-content');


  const printCount = () => {
    let length = basketCatalogProductsList.querySelector('.simplebar-content').children.length;
    basketCount.textContent = length;

    length > 0 ? basketCount.classList.add('basket__count--visible') : basketCount.classList.remove('basket__count--visible')
  }

  const generateBasketProduct = (img, title, price, id) => {
    return `
                <li class="basket-content__item basket-content-catalog__item" data-id="${id}">
                    <article class="basket-content-catalog__product basket-product basket-product-catalog" >
                    <a href="cart.html" class="product__cart">
                    <img src="${img}" alt="${title}" class="basket-product-catalog__img">
                    </a>
                        <div class="basket-product__text">
                            <h3 class="basket-product__title">${title}</h3>
                            <span class="basket-product-catalog__price">${normalPrice(price)} руб</span>
                        </div>
                        <button class="basket-product-catalog__delete" aria-label="Удалить товар">
                        </button>
                    </article>
                </li>
            `
  }

  productsBtn.forEach(el => {
    el.addEventListener('click', (e) => {
      const self = e.currentTarget;
      let parent = self.closest('.product');
      let id = parent.dataset.id;
      let img = parent.querySelector('.product__image-catalog img').getAttribute('src');
      let title = parent.querySelector('.product__title').textContent;
      let priceNumber = parseInt(priceWithoutSpaces(parent.querySelector('.product-price__current').textContent));

      plusFullPrice(priceNumber);
      printFullPrice();

      document.querySelector('.basket__btn').classList.remove('basket__btn--inactive');

      basketCatalogProductsList.querySelector('.simplebar-content').insertAdjacentHTML('afterbegin', generateBasketProduct(img, title, priceNumber, id));

      self.classList.add('product__btn--disabled');
      printCount();

    });
  });

  basketCatalogProductsList.addEventListener('click', (e) => {

    if (e.target.classList.contains('basket-product-catalog__delete')) {
      const self = e.target;
      const parent = self.closest('.basket-content-catalog__item');
      const price = parseInt(priceWithoutSpaces(parent.querySelector('.basket-product-catalog__price').textContent))
      const id = parent.dataset.id;


      document.querySelector(`.add-to-basket-btn[data-id="${id}"]`).classList.remove('product__btn--disabled');

      parent.remove();

      minusFullPrice(price);
      printFullPrice();

      let num = document.querySelectorAll('.basket-content-catalog__item').length

      if (num == 0) {
        basketCount.classList.remove('basket__count--visible');
        basketContent.classList.remove('basket-content--visible');
        document.querySelector('.basket__btn').classList.add('basket__btn--inactive');
      }

      printCount(num);

    }
  });
};

const openOrdermodal = document.querySelector('.basket-content__btn');
const orderCatalogModalList = document.querySelector('.basket-modal-order__list')
const orderModalQuantity = document.querySelector('.basket-modal-order__qantity span');
const orderModalSumm = document.querySelector('.basket-modal-order__summ span');
const orderCatalogModalShow = document.querySelector('.basket-modal-order-catalog__show');
const basketCount = document.querySelector('.basket__count');
const basketCatalogProductsList = document.querySelector('.basket-content-catalog__list');
const basketContent = document.querySelector('.basket-content');


orderCatalogModalList.addEventListener('click', (e) => {
  const printCount = () => {
    let length = basketCatalogProductsList.querySelector('.simplebar-content').children.length;
    basketCount.textContent = length;

    length > 0 ? basketCount.classList.add('basket__count--visible') : basketCount.classList.remove('basket__count--visible')
  }

  if (e.target.classList.contains('basket-product-catalog__delete')) {
    const self = e.target;
    const parent = self.closest('.basket-content-catalog__item');
    const price = parseInt(priceWithoutSpaces(parent.querySelector('.basket-product-catalog__price').textContent))
    const id = parent.dataset.id;


    document.querySelector(`.add-to-basket-btn[data-id="${id}"]`).classList.remove('product__btn--disabled');

    parent.remove();
    document.querySelector(`.basket-content-catalog__item[data-id="${id}"]`).remove();



    minusFullPrice(price);
    printFullPrice();

    orderModalQuantity.textContent = `${document.querySelectorAll('.basket-modal-order__list .basket-content-catalog__item').length} шт`;
    orderModalSumm.textContent = `${fullPrice.textContent}`;

    setTimeout(() => {
      let num = document.querySelectorAll('.basket-content-catalog__item').length

      if (num == 0) {
        basketCount.classList.remove('basket__count--visible');
        basketContent.classList.remove('basket-content--visible');
        document.querySelector('.basket__btn').classList.add('basket__btn--inactive');
      }
      printCount(num);
    }, 200)


  }
});



