
const catalogList = document.querySelector('.rating__list');
const catalogMore = document.querySelector('.show-more');
const offersSlider = document.querySelector('.offers-slider .swiper-wrapper')
const catalogListPage = document.querySelector('.catalog-grid__list');


let modal = null;

let offersQuantity = 6;
let offersLength = null;
let prodQuantity = 8;

if (window.innerWidth > 1025) {
  prodQuantity = 8;
}
if (window.innerWidth > 0 && window.innerWidth < 1025) {
  prodQuantity = 6;

}


let catalogQuantity = 9;



const randomId = () => {
  return Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
};

const normalPrice = (str) => {
  return String(str).replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ');
};

const priceWithoutSpaces = (str) => {
  return str.replace(/\s/g, '');
};





if (offersSlider) {
  const offersProducts = (offersQuantity = 6) => {
    fetch('../offers/offers.json')
      .then((response) => {
        return response.json();
      })
      .then((offers) => {
        offersLength = offers.length;

        offersSlider.innerHTML = '';

        for (let i = 0; i < offersLength; i++) {
          if (i < offersQuantity) {
            let itemOffers = offers[i];
            let id = randomId();
            offersSlider.innerHTML += `
                            <div class="swiper-slide swiper-slide-offers ">
                                <article class="product" data-id="${id}">
                                    <div class="product__discont">
                                    ${normalPrice(itemOffers.discount)}
                                    </div>
                                    <a href="cart.html" class="product__cart">
                                    <div class="product__image">
                                        <img  src="${itemOffers.mainImage}" alt="${itemOffers.mainImage}">
                                    </div>
                                    </a>

                                    <h3 class="mp-reset product__title">${itemOffers.title} "${itemOffers.model}"</h3>
                                    <div class="product__price product-price">
                                        <span class="product-price__current">${normalPrice(itemOffers.price)} руб</span>
                                        <span class="product-price__old">${normalPrice(itemOffers.oldPrice)} руб</span>
                                    </div>
                                    <button class="btn add-to-basket-btn product__btn btn--stroke btn--stroke-ef  btn-reset" data-id="${id}">Купить</button>
                                </article>
                            </div>
                            `
          }
        }

      })

  }

  offersProducts(offersQuantity);
}


