import { gsap } from "gsap";

const map = document.querySelector('.contact__map');

if (map) {
  ymaps.ready(init);
  function init() {
    // Создание карты.
    if (window.innerWidth > 0 && window.innerWidth < 769) {
      const myMap = new ymaps.Map("map", {
        center: [55.750616, 37.641809],

        zoom: 15
      });

      const myGeoObject1 = new ymaps.GeoObject({
        geometry: {
          type: "Point", // тип геометрии - точка
          coordinates: [55.769812, 37.638503] // координаты точки
        }
      });
      const myPlacemark = new ymaps.Placemark([55.750616, 37.641809], {}, {

        iconLayout: 'default#image',
        iconImageHref: '../img/map-point.svg',
        iconImageSize: [32, 22],
        iconImageOffset: [-3, -42]
      });

      myMap.geoObjects.add(myPlacemark);
      const closeM = document.querySelector("#closeM");
      var tnm = gsap.timeline({ paused: true });

      myPlacemark.events.add('click', function () {

        tnm.play();

      });
      tnm.from(".contact__card", {
        opacity: 0,
        duration: 1,
        visibility: "hidden",
        easy: "expo"
      })
      closeM.onclick = function () {
        tnm.reverse();
      };
    }
    if (window.innerWidth > 769) {
      const myMap = new ymaps.Map("map", {
        center: [55.752141, 37.623775],

        zoom: 15
      });

      const myGeoObject1 = new ymaps.GeoObject({
        geometry: {
          type: "Point", // тип геометрии - точка
          coordinates: [55.769812, 37.638503] // координаты точки
        }
      });
      const myPlacemark = new ymaps.Placemark([55.750616, 37.641809], {}, {

        iconLayout: 'default#image',
        iconImageHref: '../img/map-point.svg',
        iconImageSize: [32, 22],
        iconImageOffset: [-3, -42]
      });

      myMap.geoObjects.add(myPlacemark);
      const closeM = document.querySelector("#closeM");
      var tnm = gsap.timeline({ paused: true });

      myPlacemark.events.add('click', function () {

        tnm.play();

      });
      tnm.from(".contact__card", {
        opacity: 0,
        duration: 1,
        visibility: "hidden",
        easy: "expo"
      })
      closeM.onclick = function () {
        tnm.reverse();
      };
    }




  }
}









